<template>
  <a-row type="flex" justify="end"  style="min-width: 1400px">
    <a-col style="width: 85%">
      <div>
        <div class="mlist-control ant-list-bordered">
          <div class="list-control-finish">
            <a-radio-group :value="authState" @change="handleTypeChange">
              <a-radio-button value="全部" name="全部">全部</a-radio-button>
              <a-radio-button value="审核中" name="审核中">审核中</a-radio-button>
              <a-radio-button value="未提交" name="未提交">未提交</a-radio-button>
              <a-radio-button value="未通过" name="未通过">未通过</a-radio-button>
              <a-radio-button value="已通过" name="已通过">已通过</a-radio-button>
            </a-radio-group>
          </div>
        </div>

        <a-list
          class="listpage"
          :bordered="true"
          :pagination="paginationProps"
          :loading="loading"
          itemLayout="horizontal"
          :dataSource="data"
          :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }"
        >
          <a-list-item slot="renderItem" slot-scope="item">
            <a-button v-if="item.authState==='审核中'" type="primary" slot="actions" @click="getEntDetail(item.id)">审核</a-button>
            <a-button v-else type="primary" slot="actions" @click="getEntDetail(item.id)">查看</a-button>
            <a-list-item-meta>
            <a slot="title">{{item.enterName}}</a>
              <a-avatar slot="avatar" shape="square" style="width:50px;height:50px;padding-top: 25px;" :style="machineStatusStyleAvatar(item.authState)" >Co.</a-avatar>
            </a-list-item-meta>
            <span
              :style="machineStatusStyle(item.authState)"
              class="ant-list-item-meta-description"
              style="display:inline-block;width:340px;align-self:center"
            >{{ item.authState }}</span>
          </a-list-item>
          <br />
        </a-list>
        <a-layout-footer style="textAlign: center">DDDB ©2019 Created by 六芒星</a-layout-footer>
      </div>
    </a-col>
  </a-row>
</template>
<script>
import { getEnterpriseList } from "../../api/manRequest";
//import reqwest from 'reqwest'

export default {
  data() {
    return {
      id: localStorage.getItem("userId"),
      loading: false,
      loadingMore: false,
      showLoadingMore: true,
      authState: "全部",
      entList: [],
      filterList: [],
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  computed: {
    list() {
      // return this.$store.state.taskList;
    },
    paginationProps() {
      let pag = {
        pageSize: 6,
        total: this.data.length
      };
      return pag;
    },
    data() {
      let list = [...this.entList];
      if (this.authState === "审核中") {
        list = list.filter(item => item.authState == "审核中");
      } else if (this.authState === "未通过") {
        list = list.filter(item => item.authState == "未通过");
      } else if (this.authState === "已通过") {
        list = list.filter(item => item.authState == "已通过");
      } else if (this.authState === "未提交") {
        list = list.filter(item => item.authState == "未提交");
      }
      return list;
    }
  },
  mounted() {
    this.getEnterpriseList();
  },
  created() {
    this.$parent.showSiderMenu = true;
    this.$parent.showMenu = false;
  },
  methods: {
    // 动态样式
    machineStatusStyleAvatar(e){
      if(e == '审核中'){
        return {
          'background-color':'#D39846'
        }
      }else if(e == '未通过'){
        return {
          'background-color':'#ABABAA'
        }
      }else if(e == '已通过'){
        return {
          'background-color':'#4F5F46'
        }
      };
    },
    machineStatusStyle(e){
      if(e == '审核中'){
        return {
          'color':'#D39846'
        }
      }else if(e == '未通过'){
        return {
          'color':'#ABABAA'
        }
      } else if(e == '已通过'){
        return {
          'color':'#4F5F46'
        }
      };
    },
    getEnterpriseList() {
      getEnterpriseList()
        .then(response => {
          this.entList = response.data.data;
          console.log(this.entList);
        })
        .catch(function(error) {
          console.log(error);
        });
    },

    handleTypeChange(e) {
      this.authState = e.target.value;
    },
    onSearch() {},
    getEntDetail(itemId) {
      this.$router.push({
        name: "entInfo",
        query: {
          id: itemId
        }
      });
    }
  }
};
</script>
<style scoped>
.list-control {
  background: #fff;
  width: 90%;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 16px;
  /*height: 60px;*/
  display: flex;
  flex-direction: row;
}
.list-control-finish {
  margin-right: 50px;
}
.list-control-new {
  width: 150px;
  margin-left: 450px;
}

.listpage {
  min-height: 500px;
  background: #fff;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 16px 40px 0px 40px;
  width: 90%;
}
.Content {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.ant-list-item-meta-title :global(a){
  color: rgba(0, 0, 0, 0.65);
  font-weight: bold;
}
</style>



